<script setup lang="ts">
import OBSWebSocket from "obs-websocket-js";
import { VXETable } from "vxe-table";
const oldPw = JSON.parse(window.localStorage.getItem("obs-data") ?? "{}")?.password;
const data = useStorage(
  "obs2-data",
  {
    port: "4455",
    password: oldPw ?? "",
    greaterThanOrEqualTo: 4,
    partyLength: 1,
    autoHide: false,
  },
  localStorage,
  { mergeDefaults: true },
);
const status = reactive({
  connecting: false,
  recording: false,
  inACTCombat: false,
});
const obs = new OBSWebSocket();
const showSettings = ref(true);
onMounted(async () => {
  showSettings.value = !data.value.autoHide;
  obs.on("ExitStarted", onConnectionClosed);
  obs.on("ConnectionClosed", onConnectionClosed);
  obs.on("ConnectionError", onConnectionClosed);
  obs.on("RecordStateChanged", onRecordStateChanged);
  addOverlayListener("onLogEvent", handleLogEvent);
  addOverlayListener("onInCombatChangedEvent", handleInCombatChanged);
  addOverlayListener("PartyChanged", handlePartyChanged);
  startOverlayEvents();
  if (data.value.password !== "") {
    await connect(false);
    obs.call("GetRecordStatus").then((v) => (status.recording = v.outputActive));
  } else {
    VXETable.modal.alert({ content: "", title: "", width: "80%", size: "mini" });
  }
});
onUnmounted(() => {
  obs.off("ExitStarted", onConnectionClosed);
  obs.off("ConnectionClosed", onConnectionClosed);
  obs.off("ConnectionError", onConnectionClosed);
  obs.off("RecordStateChanged", onRecordStateChanged);
  removeOverlayListener("onLogEvent", handleLogEvent);
  removeOverlayListener("onInCombatChangedEvent", handleInCombatChanged);
  removeOverlayListener("PartyChanged", handlePartyChanged);
  obs.disconnect();
});
async function connect(showTips = false) {
  return obs
    .connect(`ws://127.0.0.1:${data.value.port}`, data.value.password)
    .then((_v) => {
      VXETable.modal.message({
        content: "",
        status: "success",
        width: "7rem",
        size: "mini",
        top: 8,
      });
      status.connecting = true;
      showSettings.value = false;
    })
    .catch((_e) => {
      if (showTips)
        VXETable.modal.message({
          content: "",
          status: "error",
          width: "7rem",
          size: "mini",
          top: 8,
        });
      status.connecting = false;
    });
}
async function start(restart = true, justDoIt = false) {
  if (!justDoIt && data.value.partyLength < data.value.greaterThanOrEqualTo) return Promise.resolve();
  if (!status.connecting) await connect();
  if (status.recording && restart) {
    obs
      .call("StopRecord")
      .then(() => setTimeout(() => obs.call("StartRecord").catch(() => {}), 1500))
      .catch(() => {});
  } else obs.call("StartRecord").catch(() => {});
}
async function stop() {
  if (status.recording) return obs.call("StopRecord").catch(() => {});
  return Promise.resolve();
}
function onConnectionClosed() {
  status.connecting = false;
}
function onRecordStateChanged(e: { outputActive: boolean; outputState: string }) {
  switch (e.outputState) {
    case "OBS_WEBSOCKET_OUTPUT_STARTED":
      status.recording = true;
      return;
    case "OBS_WEBSOCKET_OUTPUT_STOPPED":
      status.recording = false;
      return;
  }
}
async function handleLogEvent(e: { detail: { logs: string[] } }) {
  for (const log of e.detail.logs) {
    if (/^.{14} \w+ 00:(?:00B9|0[12]39)::?(?:|Battle commencing in |)\d+[^(]+[(]/i.test(log)) start();
    else if (
      /^.{14} (?:Director 21:.{8}:4000000[3F]|ChatLog 00:0038::end$|ChatLog 00:(?:00B9|0[12]39)::(?:.+|Countdown canceled by .+\.|.+))/i.test(
        log,
      )
    )
      stop();
  }
}
async function handleInCombatChanged(ev: any) {
  if (!status.inACTCombat && ev.detail.inACTCombat) start(false);
  if (status.inACTCombat && !ev.detail.inACTCombat) stop();
  status.inACTCombat = ev.detail.inACTCombat;
}
function handlePartyChanged(e: { party: any[] }) {
  // data.value.partyLength = Math.max(e.party.length, 1);
  data.value.partyLength = e.party?.length ?? 0;
}
</script>

<template>
  <header>
    <div>
      <i class="vxe-icon-dot icon" :style="{ color: status.recording ? 'red' : 'gray', textShadow: '0px  0px 3px black' }"></i>
      <vxe-button class="btns" icon="vxe-icon-caret-right" v-show="!status.recording" @click="start(false, true)"></vxe-button>
      <vxe-button class="btns" icon="vxe-icon-close" v-show="status.recording" @click="stop" size="mini"></vxe-button>
      <vxe-button class="btns" icon="vxe-icon-setting" @click="showSettings = !showSettings" size="mini"></vxe-button>
      <vxe-button class="btns" icon="vxe-icon-cut" v-show="status.recording" @click="start(true, true)" size="mini"></vxe-button>
    </div>
  </header>
  <main p-b-2 v-show="showSettings">
    <vxe-form :data="data">
      <vxe-form-item span="24">
        <template #default="{ data }">
          
          <vxe-input v-model="data.greaterThanOrEqualTo" size="small" type="integer" min="0" max="8" style="width: 3rem; margin-right: 5px"></vxe-input>
        </template>
      </vxe-form-item>
      <vxe-form-item span="24">
        <template #default="{ data }">
          
          <vxe-input v-model="data.port" size="small" placeholder="" style="width: 4rem; margin-right: 5px"></vxe-input>
        </template>
      </vxe-form-item>
      <vxe-form-item span="24">
        <template #default="{ data }">
          
          <vxe-input v-model="data.password" size="small" placeholder="" type="password" style="width: 12rem"></vxe-input>
          <vxe-button
            content=""
            size="mini"
            icon="vxe-icon-question"
            type="text"
            status="info"
            @click="VXETable.modal.message({ content: 'ACT' })"
          ></vxe-button>
        </template>
      </vxe-form-item>
    </vxe-form>
    <vxe-button size="mini" :status="'primary'" icon="vxe-icon-swap" content="" @click="connect(true)"></vxe-button>
    <el-checkbox style="margin-left: 1em" v-model="data.autoHide" label="" />
  </main>
</template>
<style lang="scss">
body {
  padding: 0;
  margin: 0.3rem 0;
  overflow-x: hidden;
}
::-webkit-scrollbar {
  width: 5px;
  height: 5px;
}
::-webkit-scrollbar-track {
  background-color: rgba(51, 51, 51, 1);
}
::-webkit-scrollbar-thumb {
  height: 30px;
  border-radius: 5px;
  background-color: rgba(216, 216, 216, 0.4);
}
::-webkit-scrollbar-thumb:active {
  background-color: rgba(160, 160, 160, 1);
}
header {
  > div {
    position: relative;
    top: 0.25rem;
    left: 0.25rem;
    .icon {
      float: left;
    }
    .btns {
      background-color: white;
      float: left;
      padding: 0.1rem 0rem 1rem 0.1rem !important;
      margin: 0rem 0.2rem 0rem 0rem !important;
      height: 1rem !important;
      width: 1.25rem !important;
    }
  }
}
main {
  border-radius: 0.3rem;
  background-color: white;
  padding: 1rem;
  padding-top: 1.5rem;
  margin: 0;
}
</style>
